import { useState } from "react";
import { useSearchParams } from "react-router-dom";
import useFetch from "../hooks/useFetch";
import moment from "moment";
import Spinner from "react-bootstrap/Spinner";
import LaunchModal from "../components/LaunchModal";
import Pagination from "../components/Pagination";
import LaunchTable from "../components/LaunchTable";
import LaunchFilters from "../components/LaunchFilters";

const HomePage = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const [query, setQuery] = useState(searchParams.get("query") || "");
  const limit = searchParams.get("limit") || 10;
  const offset = searchParams.get("offset") || 0;
  const [start, setStart] = useState(searchParams.get("start") || "");
  const [end, setEnd] = useState(searchParams.get("end") || "");
  const [show, setShow] = useState(false);
  const [modalData, setModalData] = useState({});
  const [calendar, setCalendar] = useState(
    start === "" ? "frombegining" : "pastsixmonths"
  );

  const res = useFetch(
    query === "upcoming"
      ? `launches/${query}?&limit=${limit}&offset=${offset}&start=${start}&end=${end}`
      : query === "successful"
      ? `launches/?launch_success=true&limit=${limit}&offset=${offset}&start=${start}&end=${end}`
      : query === "failed"
      ? `launches/?launch_success=false&limit=${limit}&offset=${offset}&start=${start}&end=${end}`
      : query === ""
      ? `launches/${query}?&limit=${limit}&offset=${offset}&start=${start}&end=${end}`
      : ""
  );

  if (res.isLoading) {
    return (
      <div className="d-flex justify-content-center">
        <Spinner role="status" animation="border" variant="dark" size="lg" />
      </div>
    );
  }
  if (res.error) {
    return (
      <div className="d-flex justify-content-center">
        <p className="text-danger">{JSON.stringify(res.error)}</p>
      </div>
    );
  }

  const handleChange = (event) => {
    const newQuery = event.target.value;
    setQuery(newQuery);
    setSearchParams({
      query: newQuery,
      limit: 10,
      offset: 0,
      start: start,
      end: end,
    });
  };

  const handleCalendarChange = (event) => {
    if (event.target.value === "pastsixmonths") {
      setCalendar(event.target.value);
      let date = new Date();
      let endDate = moment(date).format("YYYY-MM-DD");
      date.setMonth(new Date().getMonth() - 6);
      let startDate = moment(date).format("YYYY-MM-DD");
      setStart(startDate);
      setEnd(endDate);
      setSearchParams({
        query: query,
        limit: 10,
        offset: offset,
        start: startDate,
        end: endDate,
      });
    } else if (event.target.value === "frombegining") {
      setCalendar(event.target.value);
      setStart("");
      setEnd("");
      setSearchParams({
        query: query,
        limit: 10,
        offset: offset,
        start: "",
        end: "",
      });
    } else {
      setCalendar("frombegining");
      setSearchParams({
        query: query,
        limit: 10,
        offset: offset,
        start: start,
        end: end,
      });
    }
  };

  const handleClick = (clickedButton) => {
    setSearchParams({
      query: query,
      limit: 10,
      offset:
        clickedButton === "next" ? Number(offset) + 10 : Number(offset) - 10,
      start: start,
      end: end,
    });
  };

  const handleClose = () => setShow(false);
  const handleShow = (data) => {
    setModalData(data);
    setShow(true);
  };
  const openLink = (data) => {
    window.open(data, "_blank");
  };

  return (
    <div className="container-fluid clearfix">
      <LaunchFilters
        query={query}
        calendar={calendar}
        handleCalendarChange={handleCalendarChange}
        handleChange={handleChange}
      />
      <LaunchTable res={res} handleShow={handleShow} />
      <Pagination offset={offset} res={res} handleClick={handleClick} />
      <LaunchModal
        show={show}
        modalData={modalData}
        handleClose={handleClose}
        openLink={openLink}
      />
    </div>
  );
};

export default HomePage;
