import React from "react";
import Table from "react-bootstrap/Table";
import moment from "moment";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Badge from "react-bootstrap/Badge";

//Launch modal props:
// state: show
// functions: handleClose, openLink
// data: modalData

function LaunchModal({ show, handleClose, modalData, openLink }) {
  return (
    <Modal show={show} onHide={handleClose}>
      <Modal.Body>
        <Container className="d-flex justify-content-end">
          <Button variant="outline-danger" onClick={handleClose}>
            X
          </Button>
        </Container>
        <Container>
          <Row>
            <Col xs={4} md={4}>
              <img
                className="img-fluid"
                src={`${modalData.links?.mission_patch_small}?w=161&fit=crop&auto=format`}
                srcSet={`${modalData.links?.mission_patch_small}?w=161&fit=crop&auto=format&dpr=2 2x`}
                alt={"Rocket"}
                loading="lazy"
              />
            </Col>
            <Col xs={8} md={8}>
              <Row>
                <Col xs={12} md={12}>
                  <h2>
                    {modalData.mission_name}
                    <span className="badgeFontSize">
                      {modalData?.launch_success ? (
                        <Badge pill bg="success">
                          Success
                        </Badge>
                      ) : modalData?.upcoming ? (
                        <Badge pill bg="warning">
                          Upcoming
                        </Badge>
                      ) : modalData?.launch_failure_details ? (
                        <Badge pill bg="danger">
                          Failed
                        </Badge>
                      ) : (
                        ""
                      )}
                    </span>
                  </h2>
                </Col>
                <Col xs={12} md={12}>
                  <h6 className="text-muted">
                    {modalData.rocket?.rocket_name}
                  </h6>
                </Col>
                <Col xs={12} md={12}>
                  <img
                    className="cursor"
                    onClick={() => openLink(modalData?.links?.article_link)}
                    src="https://img.icons8.com/color/48/000000/nasa.png"
                    alt="NASA"
                  />
                  <img
                    className="cursor"
                    onClick={() => openLink(modalData?.links?.wikipedia)}
                    src="https://img.icons8.com/color/48/000000/wikipedia-logo.png"
                    alt="Wikipedia"
                  />
                  <img
                    className="cursor"
                    onClick={() => openLink(modalData?.links?.video_link)}
                    src="https://img.icons8.com/fluency/48/000000/youtube-play.png"
                    alt="Youtube"
                  />
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
        <br />
        <h6>
          {modalData.details}
          {modalData.details && modalData?.links?.wikipedia && (
            <span
              onClick={() => openLink(modalData?.links?.wikipedia)}
              className="text-primary cursor"
            >
              {" "}
              Wikipedia
            </span>
          )}
        </h6>
        <Table responsive>
          <tbody>
            <tr>
              <td>Flight Number</td>
              <td>{modalData.flight_number}</td>
            </tr>
            <tr>
              <td>Mission Name</td>
              <td>{modalData.mission_name}</td>
            </tr>
            <tr>
              <td>Rocket Type</td>
              <td>{modalData.rocket?.rocket_type}</td>
            </tr>
            <tr>
              <td>Rocket Name</td>
              <td>{modalData.rocket?.rocket_name}</td>
            </tr>
            <tr>
              <td>Manufacturer</td>
              <td>
                {modalData.rocket?.second_stage.payloads[0]?.manufacturer}
              </td>
            </tr>
            <tr>
              <td>Nationality</td>
              <td>
                {modalData.rocket?.second_stage?.payloads[0]?.nationality}
              </td>
            </tr>
            <tr>
              <td>Launch Date</td>
              <td>
                {moment(modalData.launch_date_utc).format("DD MMMM YYYY HH:mm")}
              </td>
            </tr>
            <tr>
              <td>Payload Type</td>
              <td>
                {modalData.rocket?.second_stage?.payloads[0]?.payload_type}
              </td>
            </tr>
            <tr>
              <td>Orbit</td>
              <td>{modalData.rocket?.second_stage?.payloads[0]?.orbit}</td>
            </tr>
            <tr>
              <td>Launch Site</td>
              <td>{modalData.launch_site?.site_name}</td>
            </tr>
          </tbody>
        </Table>
      </Modal.Body>
    </Modal>
  );
}

export default LaunchModal;
