import React from "react";
import Table from "react-bootstrap/Table";
import moment from "moment";
import uuid from "react-uuid";
import Badge from "react-bootstrap/Badge";

//props
// data: res
// function: handleShow

function LaunchTable({ res, handleShow }) {
  const tableHeaders = [
    { id: 1, value: "No:" },
    { id: 2, value: "Launched (UTC)" },
    { id: 3, value: "Location" },
    { id: 4, value: "Mission" },
    { id: 5, value: "Orbit" },
    { id: 6, value: "Launch Status" },
    { id: 7, value: "Rocket" },
  ];

  return (
    <Table responsive>
      <thead>
        <tr>
          {tableHeaders.map((item) => (
            <th key={item.id}>{item.value}</th>
          ))}
        </tr>
      </thead>
      <tbody>
        {res?.data.length > 0 ? (
          res.data?.map((item) => (
            <tr
              className="cursor"
              key={uuid()}
              onClick={() => handleShow(item)}
            >
              <td>{item?.flight_number}</td>
              <td>
                {moment(item?.launch_date_utc).format("DD MMMM YYYY HH:mm:ss")}
              </td>
              <td>{item?.launch_site?.site_name}</td>
              <td>{item?.mission_name}</td>
              <td>{item?.rocket?.second_stage?.payloads[0]?.orbit}</td>
              <td>
                {item?.launch_success ? (
                  <Badge pill bg="success">
                    Success
                  </Badge>
                ) : item?.upcoming ? (
                  <Badge pill bg="warning">
                    Upcoming
                  </Badge>
                ) : item?.launch_failure_details ? (
                  <Badge pill bg="danger">
                    Failed
                  </Badge>
                ) : (
                  ""
                )}
              </td>
              <td>{item?.rocket?.rocket_name}</td>
            </tr>
          ))
        ) : (
          <tr>
            <td colSpan={7}>
              <h3 className="text-center">
                {" "}
                No results found for the specified filter
              </h3>
            </td>
          </tr>
        )}
      </tbody>
    </Table>
  );
}

export default LaunchTable;
